import ErrorStackParser from 'error-stack-parser';
import { record } from 'rrweb';
import { onLCP, onFID, onCLS, onFCP, onTTFB } from 'web-vitals';
import { EVENTTYPES, HTTP_CODE, STATUS_CODE } from '../shared';
import { transportData, breadcrumb, resourceTransform, httpTransform, options } from '../core';
import { getTimestamp, parseUrlToObj, unknownToString, getResource, on, _global, _support, zip, generateUUID } from '../utils';
const HandleEvents = {
  /**
   * xhrfetch
   */
  handleHttp(data, type) {
    const isError = data.status === 0 || data.status === HTTP_CODE.BAD_REQUEST || data.status > HTTP_CODE.UNAUTHORIZED;
    const result = httpTransform(data);
    // 
    breadcrumb.push({
      type,
      category: breadcrumb.getCategory(type),
      data: Object.assign({}, result),
      status: isError ? STATUS_CODE.ERROR : STATUS_CODE.OK,
      time: data.time
    });
    if (isError) {
      // 
      transportData.send({ ...result, type, status: STATUS_CODE.ERROR });
    }
  },
  handleError(ev) {
    try {
      const target = ev.target;
      // Vue.config.errorHandler or 
      if (!target || (ev.target && !ev.target.localName)) {
        // errorHandlerevev.error
        let stackFrame = ErrorStackParser.parse(!target ? ev : ev.error)[0];
        let { fileName, columnNumber, lineNumber } = stackFrame;
        let errorData = {
          type: EVENTTYPES.ERROR,
          status: STATUS_CODE.ERROR,
          time: getTimestamp(),
          message: ev.message,
          fileName,
          line: lineNumber,
          column: columnNumber
        };
        breadcrumb.push({
          type: EVENTTYPES.ERROR,
          // 
          category: breadcrumb.getCategory(EVENTTYPES.ERROR),
          data: errorData,
          time: getTimestamp(),
          status: STATUS_CODE.ERROR
        });
        return transportData.send(errorData);
      }

      //  ok 
      if (target.localName) {
        //  
        const data = resourceTransform(target);
        breadcrumb.push({
          ...data,
          type: EVENTTYPES.RESOURCE,
          //  Resource
          category: breadcrumb.getCategory(EVENTTYPES.RESOURCE),
          status: STATUS_CODE.ERROR,
          time: getTimestamp()
        });
        return transportData.send({
          ...data,
          type: EVENTTYPES.RESOURCE,
          status: STATUS_CODE.ERROR
        });
      }
    } catch (er) {
      console.error(':', er);
    }
  },
  handleHistory(data) {
    const { from, to } = data;
    // parsedFromrelative
    const { relative: parsedFrom } = parseUrlToObj(from);
    const { relative: parsedTo } = parseUrlToObj(to);
    breadcrumb.push({
      type: EVENTTYPES.HISTORY,
      category: breadcrumb.getCategory(EVENTTYPES.HISTORY),
      data: {
        from: parsedFrom ? parsedFrom : '/',
        to: parsedTo ? parsedTo : '/'
      },
      time: getTimestamp(),
      status: STATUS_CODE.OK
    });
  },
  handleHashchange(data) {
    const { oldURL, newURL } = data;
    const { relative: from } = parseUrlToObj(oldURL);
    const { relative: to } = parseUrlToObj(newURL);
    breadcrumb.push({
      type: EVENTTYPES.HASHCHANGE,
      category: breadcrumb.getCategory(EVENTTYPES.HASHCHANGE),
      data: {
        from,
        to
      },
      time: getTimestamp(),
      status: STATUS_CODE.OK
    });
  },
  handleUnhandleRejection(ev) {
    let stackFrame = ErrorStackParser.parse(ev.reason)[0];
    let { fileName, columnNumber, lineNumber } = stackFrame;
    let data = {
      type: EVENTTYPES.UNHANDLEDREJECTION,
      status: STATUS_CODE.ERROR,
      time: getTimestamp(),
      message: unknownToString(ev.reason.message || ev.reason.stack),
      fileName,
      line: lineNumber,
      column: columnNumber
    };

    breadcrumb.push({
      type: EVENTTYPES.UNHANDLEDREJECTION,
      category: breadcrumb.getCategory(EVENTTYPES.UNHANDLEDREJECTION),
      data: Object.assign({}, data),
      time: getTimestamp(),
      status: STATUS_CODE.ERROR
    });
    transportData.send(data);
  },
  handlePerformance() {
    onLCP((res) => {
      fn(res);
    });
    onFID((res) => {
      fn(res);
    });
    onCLS((res) => {
      fn(res);
    });
    onFCP((res) => {
      fn(res);
    });
    onTTFB((res) => {
      fn(res);
    });

    function fn(res) {
      // namerating value
      let { name, rating, value } = res;
      transportData.send({
        type: EVENTTYPES.PERFORMANCE,
        status: STATUS_CODE.OK,
        time: getTimestamp(),
        name,
        rating,
        value
      });
    }

    let observer = new PerformanceObserver((list) => {
      for (const long of list.getEntries()) {
        // 
        transportData.send({
          type: EVENTTYPES.PERFORMANCE,
          name: 'long_task',
          longTask: long,
          time: getTimestamp(),
          status: STATUS_CODE.OK
        });
      }
    });
    observer.observe({ entryTypes: ['longtask'] });

    on(_global, 'load', function () {
      // 
      transportData.send({
        type: EVENTTYPES.PERFORMANCE,
        name: 'resource_list',
        time: getTimestamp(),
        status: STATUS_CODE.OK,
        resourceList: getResource()
      });

      // 
      transportData.send({
        type: EVENTTYPES.PERFORMANCE,
        name: 'memory',
        time: getTimestamp(),
        status: STATUS_CODE.OK,
        memory: performance.memory
      });
    });
  },
  handleScreen() {
    try {
      // 
      let events = [];
      // stopFn
      // let stopFn = record({
      record({
        emit(event, isCheckout) {
          if (isCheckout) {
            // 
            if (_support.hasError) {
              let recordScreenId = _support.recordScreenId;
              _support.recordScreenId = generateUUID();
              transportData.send({
                type: EVENTTYPES.RECORDSCREEN,
                recordScreenId,
                time: getTimestamp(),
                status: STATUS_CODE.OK,
                events: zip(events)
              });
              events = [];
              _support.hasError = false;
            } else {
              // 
              events = [];
              _support.recordScreenId = generateUUID();
            }
          }
          events.push(event);
        },
        recordCanvas: true,
        // 10s
        checkoutEveryNms: 1000 * options.recordScreentime
      });
    } catch (err) {
      console.err(':', err);
    }
  }
};
export { HandleEvents };
