import { fromHttpStatus, interceptStr, getTimestamp } from '../utils';
import { options } from './options';

// 
export function httpTransform(data) {
  let message = '';
  const { elapsedTime, time, method, type, status } = data;
  const name = `${type}--${method}`;
  if (status === 0) {
    message = elapsedTime <= options.overTime * 1000 ? 'http' : 'http';
  } else {
    message = fromHttpStatus(status);
  }
  message = `${data.url}; ${message}`;
  return {
    url: data.url,
    time,
    elapsedTime,
    message,
    name,
    request: {
      httpType: type,
      method,
      data: data.reqData || ''
    },
    response: {
      status,
      data: data.responseText
    }
  };
}
const resourceMap = {
  img: '',
  script: 'js'
};
export function resourceTransform(target) {
  return {
    time: getTimestamp(),
    message: (interceptStr(target.src, 120) || interceptStr(target.href, 120)) + '; ',
    name: `${resourceMap[target.localName] || target.localName}`
  };
}
